	Object.defineProperty(exports, "__esModule", { value: true });
	var React = __webpack_require__(1);
	var component_main_body_holder_1 = __webpack_require__(11);
	var component_bookshelf_intro_1 = __webpack_require__(149);
	var component_book_card_1 = __webpack_require__(147);
	var sprintBook = __webpack_require__(393);
